import React from 'react';
import HeaderContainer, { P } from './styled';
import ChangeText from '../ChangeText';

const texts = [" ", "", "", " ", "-", " ", "", "", " " ];

export default class Header extends React.Component {
  render () {
    return (
      <HeaderContainer>
        <h1>
             <br />
          <ChangeText
            texts={texts}
            animation='change'
          />
        </h1>
        <P className='mobileHide'>
          ,  <br /> &nbsp;&nbsp;&nbsp;&nbsp;.
        </P>
      </HeaderContainer>
    );
  }
}



