import React from 'react';
import { Row , Col } from 'react-flexbox-grid';
import Container, { Slides, Slide, Inner, H2 } from './styled';
import Button, { ButtonGroup } from './../Button';
import ChangeText from './../ChangeText';

const texts = [
  '',
  '',
  ' ',
  ''
];

const p = [
  '    6%  15%    . ,    ,  .',
  '       1400 ,       .',
  '    ,            .',
  '   ,   ,    .',
];

const inner = (
  <Inner>
    <h5>     I </h5>
    <Row>
      <Col xs={12} lg={7}>
        <small>
              31  2018    8096,25,       I .
        </small>
      </Col>
      <Col xs={12} lg={5} style ={{ display: 'flex', justifyContent: 'flex-end'}} >
        <ButtonGroup>
          <Button style={{padding: '13px 38px'}} inactive disabled outline type="button"></Button>
          <Button inactive disabled outline small type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            &#8226; &#8226; &#8226;
          </Button>
        </ButtonGroup>
      </Col>
    </Row>
  </Inner>
);

export default () => (
  <Container id="slides" >
    <H2>  &nbsp;</H2> <ChangeText inline texts={texts} animation='change1' />
    <p>
       . ,     &nbsp;, &nbsp;.
    </p>
    <Slides>
      <Slide id="slides_1" className="slide-xs" >
        {inner}
        <Button disabled footer type="button"></Button>
      </Slide>
      <Slide id="slides_2" className="slide-sm" >
        {inner}
        <Button disabled footer type="button"></Button>
      </Slide>
      <Slide className="slide-md" >
        {inner}
        <Button disabled footer type="button"></Button>
      </Slide>
      <Slide className="slide-lg" >
        {inner}
        <Button disabled footer type="button"></Button>
      </Slide>
    </Slides>
  </Container>
);



