import React from 'react';
import { Col } from 'react-flexbox-grid';
import Carousel from './../Carousel';
import Link from '../Link';
import Button from './../Button';

import Container, { Size, CarouselBox, CarouselItem, Modes } from './styled';

const tarifs = [
  <Size id="tariff_s">
    <img alt='' src={'images/S.svg'}/>
    <ul>
      <li> <b>90</b>   </li>
      <li> <b></b>  &nbsp; &nbsp;  &nbsp; <b>&nbsp;150&nbsp;.&nbsp; </b></li>
    </ul>
    <div>
      <h3>
        
      </h3>
    </div>
    <a href="https://sfera.ru/registration?tariff=S" target="_self">
      <Button className="choose tariff" outline wide>
         
      </Button>
    </a>
  </Size>,
  <Size id="tariff_m">
    <img alt='' src={'images/M.svg'}/>
    <ul>
      <li> <b> 5</b>  ,   40</li>
      <li> <b></b>  &nbsp; &nbsp;  &nbsp; <b>&nbsp;300&nbsp;.&nbsp; </b></li>
    </ul>
    <div>
      <h3>
        3  
      </h3>
      <small> 599&nbsp;&nbsp;</small>
    </div>
    <a href="https://sfera.ru/registration?tariff=M" target="_self">
      <Button className="choose tariff" outline wide>
         
      </Button>
    </a>
  </Size>,
  <Size id="tariff_l">
    <img alt='' src={'images/L.svg'}/>
    <ul>
      <li> <b> 15</b>  ,   30</li>
      <li> <b></b>  &nbsp; &nbsp;  &nbsp; <b>&nbsp;500&nbsp;.&nbsp; </b></li>
    </ul>
    <div>
      <h3>
        3  
      </h3>
      <small> 899&nbsp;&nbsp;</small>
    </div>
    <a href="https://sfera.ru/registration?tariff=L" target="_self">
      <Button className="choose tariff" outline wide>
         
      </Button>
    </a>
  </Size>

];
export default () => (
  <Container id="tarifs">
    <Modes>
      {tarifs.map((tarif, i) => (
        <Col key={i} xs={4}>
          {tarif}
        </Col>
      ))}
    </Modes>
    <CarouselBox>
      <Carousel
        {...{
          slidesToShow: 2,
          slidesToScroll: 1,
          arrows: false,
          autoplay: false,
          infinite: false,
        }}
      >
        {tarifs.map((tarif, i) => (
          <CarouselItem key={i} >
            {tarif}
          </CarouselItem>
        ))}
      </Carousel>
    </CarouselBox>
    <Link href='https://bcs-bank.com/f/business/sme/tariffs.pdf?v7' target='_blank' ><img alt='' style={{margin: '5px', verticalAlign: 'middle'}} src='images/pdf.png'/>  </Link>
  </Container>
);



