import React from 'react';
import Container, { P, Column, H3, Link } from './styled';
import Appear from '../Appear';
import Card from './Card';

export default () => (
  <Container id="bank_1">
    <Column xs={12} md={6}>
      <P id="card_1">
        <H3> </H3>
        <p>  &nbsp;&nbsp;</p>
      </P>
      <P id="card_2">
        <H3>    <span style={{whiteSpace: 'nowrap'}}></span></H3>
        <p>  &nbsp;15&nbsp;</p>
        <p>  &nbsp;</p>
        <Link tel href='https://sfera.ru/start/'>
           . &nbsp;&nbsp;&nbsp;
          <img alt='' src='images/arrow.svg' width="18" height="13" />
        </Link>
      </P>
      <P id="card_4">
        <H3>  &nbsp; &nbsp;  &nbsp;&nbsp; </H3>
        <p>&nbsp;150&nbsp;&nbsp; &nbsp; &nbsp;&nbsp;S<br />&nbsp; 500&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;L</p>
      </P>
    </Column>
    <Column xs={12} md={6}>
      <P id="bank_2">
        <H3> &nbsp;</H3>
        <p> , - &nbsp;&nbsp;</p>
      </P>
      <Appear className='card'>
        <Card />
      </Appear>
    </Column>
  </Container>
);



